<template>
  <div class="sp-goods-picker">
    <Tabs :value="curTab" :animated="false" @input="handleTabChange">
      <TabPane label="" name="goods">
        <GoodsPicker :value="curTab === 'goods' ? value : null" @input="handleChange" />
      </TabPane>
      <TabPane label="" name="goods-tag">
        <GoodsTagPicker :value="curTab === 'goods-tag' ? value : null" @input="handleChange" />
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
import GoodsPicker from './goods-items-picker'
import GoodsTagPicker from './goods-tag-picker'

const defaultVal = (type) => {
  if (type === 'goods') {
    return {
      type: 'goods',
      data: []
    }
  } else {
    return {
      type: 'goods-tag',
      data: {}
    }
  }
}

export default {
  components: { GoodsPicker, GoodsTagPicker },

  props: {
    value: Object,
    multiple: {
      type: Boolean,
      default: true
    }
  },

  data () {
    const { type = 'goods' } = this.value || {}

    return {
      curTab: type,
      tabIdentifier: 'sp-goods-picker',
      localVal: this.value ? this.value : defaultVal(type)
    }
  },

  methods: {
    getVal () {
      return this.localVal
    },
    handleChange (val) {
      this.localVal = val
    },
    handleTabChange (name) {
      this.curTab = name
      this.localVal = defaultVal(name)
    }
  }
}
</script>
