<template>
  <div class="sp-page-picker">
    <Form :label-width="80">
      <FormItem label="">
        <Select placeholder="" :value="curId" @input="handleChange">
          <Option v-for="item in list" :value="item.page_id" :key="item.page_id">{{
            item.title
          }}</Option>
        </Select>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import BasePicker from './base'
import { resolveSelectVal } from '../helper'

const ID_KEY = 'page_id'

export default {
  extends: BasePicker,

  config: {
    title: '',
    type: 'page'
  },

  data () {
    return {
      list: [],
      curId: resolveSelectVal(this.value, ID_KEY)
    }
  },

  created () {
    this.fetch()
  },

  methods: {
    async fetch () {
      const list = await this.$api.picker.page()
      this.list = list
      if (list.length > 0) {
        const data = this.list[0]
        this.curId = data[ID_KEY]
        this.updateVal(data)
      }
    },
    handleChange (val) {
      const data = this.list.find((t) => t[ID_KEY] === val)
      this.updateVal(data)
    }
  }
}
</script>

<style></style>
