<style lang="scss"></style>
<template>
  <div class="sp-path-page-picker">
    <Table :columns="columns" :data="list" @on-row-click="handleClickItem">
      <template slot-scope="{ row, index }" slot="selection">
        <Radio :value="curId === row.id" :key="index"/>
      </template>
    </Table>
  </div>
</template>

<script>
export default {
  name: '',
  props: {
    onSelect: Function,
    data: [Object, Array, String, Number, Boolean]
  },
  data() {
    const columns = [
      {
        slot: 'selection',
        width: '161'
      },
      {
        title: 'ID',
        key: 'id',
        width: '250'
      },
      {
        title: '',
        key: 'title',
        width: '250'
      }
    ]
    return {
      columns,
      curId: '',
      list: [
        {
          title: '',
          id: 'groups_list'
        }
      ]
    }
  },
  created() {},
  mounted() {
    this.curId = this.data.id
    console.log('path-page-picker init data: ', this.data)
  },
  methods: {
    handleClickItem(item) {
      this.curId = item.id
      this.onSelect(item)
    }
  }
}
</script>
