<style lang="scss">
.action-tools {
  left: 0;
  right: 0;
  text-align: center;
  z-index: 2000;
  position: relative;
  .icon-tuodong {
    color: $color-active;
    position: absolute;
    left: calc(50% - 8px);
    top: -1px;
    border: 1px solid $color-active;
    font-size: 24px;
    height: 24px;
    cursor: move;
    z-index: 2000;
  }
  // 
  .icon-shanchudelete31 {
    color: $color-active;
    cursor: pointer;
    font-size: 22px;
    z-index: 2000;
    transform: none;
    margin: 0 2px;
  }
  // 
  .icon-shangyi {
    color: $color-active;
    cursor: pointer;
    font-size: 22px;
    z-index: 2000;
    margin: 0 2px;
  }
  // 
  .icon-xiayi {
    color: $color-active;
    cursor: pointer;
    font-size: 22px;
    z-index: 2000;
    margin: 0 2px;
  }
  .action-tools.hovering {
    display: block;
  }
  .hover-action {
    position: absolute;
    right: 1px;
  }
}
</style>

<template>
  <div class="action-tools">
    <!-- {{this.x1}},{{this.y1}}/{{this.x2}},{{this.y2}},{{index}},{{hover}} -->
    <SpIcon icon="icon-tuodong" v-if="drag"></SpIcon>
    <div class="hover-action">
      <SpIcon icon="icon-shanchudelete31" @click.native.stop="handleDeleteWidget"></SpIcon>
      <SpIcon icon="icon-shangyi" @click.native.stop="handleMoveUpWidget" v-if="up"></SpIcon>
      <SpIcon icon="icon-xiayi" @click.native.stop="handleMoveDownWidget" v-if="down"></SpIcon>
    </div>
  </div>
</template>

<script>
// import mixins from './../../plugin/mixins'
import { log } from '@/utils'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'actionTools',
  // mixins: [mixins],
  props: {
    uuid: {
      type: String
    },
    // 
    sindex: {
      type: Number,
      default: 0
    },
    length: {
      type: Number,
      default: 0
    },
    drag: {
      type: Boolean,
      default: true
    },
    up: {
      type: Boolean,
      default: true
    },
    down: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      dragTarget: null,
      x1: 0,
      y1: 0,
      x2: 0,
      y2: 0
    }
  },
  computed: mapState({
    // 
    pagedata: (state) => state.pagedata
  }),
  created() {},
  mounted() {
    // document.getElementById('dragwidget').addEventListener('mousedown', function(e) {
    //     console.log(`hoverstyle dragwidget mousedown`)
    // })
    // 
    // console.log(``);
    // document.addEventListener("mousemove", this.handlemousemove, true);
    // // 
    // document.addEventListener("mouseup", this.handlemouseup, true);
  },
  methods: {
    ...mapMutations(['DELETE_WIDGET', 'CHANGE_WIDGET_SORT']),
    handleDeleteWidget() {
      log.debug(`[action-tools] handleDeleteWidget: `, this.uuid)
      this.DELETE_WIDGET(this.uuid)
    },
    // 
    handleMoveUpWidget() {
      if (this.sindex > 0) {
        this.changeComponentsPosition(this.sindex, this.sindex - 1)
      }
    },
    // 
    handleMoveDownWidget() {
      const { widgets } = this.pagedata
      if (this.sindex < widgets.length - 1) {
        this.changeComponentsPosition(this.sindex, this.sindex + 1)
      }
    },
    // 
    changeComponentsPosition(index1, index2) {
      this.CHANGE_WIDGET_SORT({ index1, index2 })
      // const { widgets } = this.pagedata
      // let temp = widgets[index1]
      // this.$set(widgets, index1, widgets[index2])
      // this.$set(widgets, index2, temp)
      // this.$forceUpdate()
    }
  }
}
</script>
