.sp-zoom {
  .zoom {
    margin-top: 4px;
    display: inline-block;
  }
}
</style>
<template>
  <div class="sp-zoom">
    <span class="menu-item">
      <div class="menu-item__icon">
        <!-- <Input type="text" size="small" v-model="zoom"/> -->
        <Poptip width="300">
          <span class="zoom">{{ zoom }}%</span>
          <!-- <div slot="title"><i></i></div> -->
          <div slot="content" style="margin: 0 10px;">
            <Slider v-model="szoom" :max="100" show-tip="never"></Slider>
          </div>
        </Poptip>
      </div>
    </span>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'SpZoom',
  props: {
    icon: String,
    text: String
  },
  data() {
    return {
      szoom: 0
    }
  },
  watch: {
    szoom: {
      handler: function(nval, oval) {
        console.log(nval)
        this.ZOOM_SET(nval)
      },
      deep: true
    }
  },
  computed: {
    ...mapState({
      zoom: (state) => state.zoom
    })
  },
  mounted() {
    this.szoom = this.zoom
  },
  methods: {
    ...mapMutations(['ZOOM_SET'])
  }
}
</script>
