<style lang="scss">
  .widget-W0019-props {
    .wgt-props {
      display: flex;
      align-items: center;
      margin: 20px 0 10px;

      &_img {
        flex: 0;
        width: 60px;
        height: 60px;
        cursor: pointer;

        img {
          width: 60px;
          height: 60px;
        }
      }

      &_delete {
        flex: 0;
        width: 30px;
        cursor: pointer;
        padding-top: 5px;
        text-align: center;

        .icon-shanchu {
          color: #3091f2;
        }
      }

      &_content {
        flex: 1;
        padding: 5px 10px;

        .title {
          font-size: 12px;
          font-weight: 400;
          @include mult-ellipsis(2);
        }

        .tag {
          .tag-item {
            padding: 2px 3px;
            // border: 1px solid rgba(247, 137, 137, 1);
            border-radius: 3px;
            font-size: 12px;
            color: #f56c6c;
            background-color: rgba(254, 240, 240, 1);
            margin-right: 3px;
            // @include mult-ellipsis(1)
          }
        }
      }
    }

    .tip {
      margin-top: 8px;
      color: #d01717;
    }
  }
</style>
<template>
  <div class="widget-W0019-props">
    <Tabs value="name1" :animated="false">
      <TabPane :label="`${activeElement.name}[${activeElement.type}]`" name="name1" style="overflow: auto">
        <panelBar title="">
          <attr-field label="">
            <Input v-model="activeElement.text" />
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtTitleBackgroundColor"></color-picker>
          </attr-field>
          <div class="division"></div>
          <attrField label="">
            <iSwitch size="small" v-model="activeElement.wgtTitleBorderRadius" />
          </attrField>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtTitleFontSize" size="small" :min="0"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtTitleTextColor"></color-picker>
          </attr-field>

        </panelBar>

        <panelBar title=" ">
          <attr-field label="">
            <Input v-model="activeElement.moreText" />
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtMoreTitleFontSize" size="small" :min="10"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtMoreTitleColor"></color-picker>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtMoreRightMargin" size="small" :min="0"></InputNumber>
          </attr-field>
        </panelBar>


        <panelBar title="">
          <attr-field label="">
            <color-picker v-model="activeElement.wgtShopNameColor"></color-picker>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtShopNameFontSize" size="small" :min="14"></InputNumber>
          </attr-field>
          <div class="division"></div>
          <attrField label="">
            <iSwitch size="small" v-model="activeElement.wgtShopBorder" />
          </attrField>
          <div class="division"></div>
          <attr-field label="">
            <color-picker v-model="activeElement.wgtShopBorderColor"></color-picker>
          </attr-field>
          <div class="division"></div>
          <attr-field label="">
            <InputNumber v-model="activeElement.wgtShopBorderWidth" size="small" :min="0" :max="5"></InputNumber>
          </attr-field>
        </panelBar>
      </TabPane>
      <TabPane label="" name="name2">
        <basic-set v-model="activeElement"></basic-set>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import {
    panelBar,
    attrField,
    dataBind,
    colorPicker
  } from '../../common/comps'
  import basicSet from '../../common/basic-set'
  export default {
    name: 'W0019Props',
    props: ['activeElement'],
    data() {
      return {}
    },
    components: {
      panelBar,
      attrField,
      dataBind,
      // goodsPrice,
      colorPicker,
      basicSet
    },
    methods: {
      // 
      deleteItem(i) {
        this.$Modal.confirm({
          title: '',
          content: '',
          onOk: () => {
            if (this.activeElement.data.length > 0) {
              this.activeElement.data.splice(i, 1)
              this.$Message.success('')
            }
          }
        })
      },
      handleChangeColumn(val) {
        this.column = val
        this.activeElement.columnStyle = val
      },
      async handleClickPickerGoods(item, index) {
        const {
          data
        } = await this.$picker('path', {
          data: JSON.parse(JSON.stringify(item)),
          filterMenu: ['goods'],
          activeKey: 'goods_id'
        })
        const {
          goods_id,
          pics,
          price,
          item_name,
          linkPage,
          linkPageLabel,
          market_price
        } = data
        const goodsData = {
          goods_id,
          goods_pic: pics.length > 0 ? pics[0] : '',
          price: price,
          goods_name: item_name,
          linkPage,
          linkPageLabel,
          market_price
        }
        // this.activeElement.data[index] = res.data
        this.$set(this.activeElement.data, index, goodsData)
      },
      onChangePath(data) {
        const goodsData = data.map(item => {
          const {
            goods_id,
            pics,
            price,
            item_name,
            linkPage,
            linkPageLabel,
            market_price
          } = item
          return {
            goods_id,
            goods_pic: pics.length > 0 ? pics[0] : '',
            price: price,
            goods_name: item_name,
            linkPage,
            linkPageLabel,
            market_price
          }
        })
        this.activeElement.data = goodsData
      }
    }
  }
</script>
